<template>
  <div id="translateForm">
    <form v-on:submit="formSubmit">
      <input id="text" type="text" v-model="textToTranslate" placeholder="Enter a word...">
      <select v-model="language">
        <option value="ru">Russian</option>
        <option value="es">Spanish</option>
        <option value="fr">French</option>
        <option value="de">German</option>
      </select>
      <input id="submit" type="submit" value="Translate">
    </form>
  </div>
</template>

<script>

  export default {
    name: 'translateForm',
    data(){
      return{
        textToTranslate:'',
        language :''
      }
    },
    created(){
      this.language = 'ru';
    },
    methods:{
      formSubmit(e){
        this.$emit('formSubmit',this.textToTranslate,this.language);
        e.preventDefault();
      }
    }
  }
</script>

<style>
  #app {

  }
  #submit {
    border:none;
    background-color: wheat;
    border-radius: 15px;
    outline: none;
  }
  select {
    border:none;
    outline: none;
    border-radius: 15px;
  }
  #text{
    border:none;
    outline: none;
    border-radius: 10px;
  }
</style>



